import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { useStaticQuery, graphql } from "gatsby"
import { ParallaxProvider } from "react-scroll-parallax"
import ParallaxBanner from "../components/parallaxBanner"

const GolvavjamningPage = () => {
    const { gcms } = useStaticQuery(graphql`
    {
        gcms {
            golvavjamningHeroes {
                hero {
                  url
                }
              }
        }
    }
  `)
    return (
        <Layout>
            <SEO title="Golvavjämning" />
            <ParallaxProvider>
                <div class="cntnr column-flex">
                    <ParallaxBanner url={gcms.golvavjamningHeroes[0].hero.url} topMargin="-40px" amount={0.8} heading="Golvavjämning"/>
                    <section class="content-section">
                    <h1 class="section-header">Golvavjämning</h1><br/>
                        <p>
                            Syftet med golvavjämning är att  skapa en planhet som är klar för golvläggning.
                            Ojämna undergolv kan leda till sprickor  i golven och knarrljud. Vi utför
                            sandavjämning om golvet ligger på sand, eller flytspackling om golvet ligger
                            på betong. Vid behov av anpassning och justering av ojämnheter bidrar
                            golvavjämning till en slät och slitstark yta. En golvavjämning ger dig en stabil
                            grund för senare installation av valfritt ytskikt.



                    </p>
                        <p>
                            Upplever du att ditt golv är ojämnt, har sprickor eller knarrar?
                            Tveka inte att höra av dig till oss!
                    </p>
                    </section>

                </div>
            </ParallaxProvider>
        </Layout>
    )
}

export default GolvavjamningPage;